// main: ../style.scss

.footer {
  padding: 1rem 0;

  background-color: #000;
}

.footer__inner {
  display: flex;
  justify-content: space-between;
}

.footer__credits {
  display: flex;
  align-items: center;
}

.footer__copyright {
  padding-right: .8rem;
  margin-right: 1rem;

  font-size: .94rem;
  color: #BBBBCC;

  border-right: 1px solid fade(#000, 35%);
}

.footer__social {
  display: flex;
  align-items: center;

  a {
      margin-right: 1.2rem;
      text-decoration: none;
  }
}

.footer__nav {
  display: flex;
  align-items: center;
  font-size: .94rem;
}

.footer__nav-link {
  margin-left: 1.5rem;

  color: #BBBBCC;
  text-decoration: none;

  &:hover {
      text-decoration: underline;
  }


  &--btn {
      padding: .5rem 1.4rem;

      border-radius: 20px;
      border: 1px solid #000;
      line-height: 1.1;

      &:hover {
          text-decoration: none;
          background-color: #BBBBCC;
          border-color: #BBBBCC;
          color: #000;
      }
  }
}
