// main: ../style.scss

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 1.05em 1.7em;
  min-width: 170px;

  border: 1px solid #000;
  border-radius: 1.8em;
  background: none;
  cursor: pointer;

  font-family: inherit;
  font-size: 1.06rem;
  line-height: 1.1;
  color: #000;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;

  transition: background-color .2s linear, color .2s linear, box-shadow .2s linear, border-color .2s linear;

  &:focus {
      outline: none;
  }

  &:hover {
      background-color: #000;
      box-shadow: 2px 2px 10px fade(#000, 24%);
      color: #fff;
      border-color: #000;
  }

  + .btn {
      margin-left: 1rem;
  }



  /* Modificators */
  &--sm {
      padding: .7em 1.2em;
      min-width: 0;

      font-size: .94rem;
      font-weight: 700;
      border-width: 2px;
  }

  &--thin {
      min-width: 145px;

      font-size: .8rem;
  }

}
