// main: ../style.less

.about {
  margin: 8.4rem 0;
}

.about__inner {
  display: flex;
  max-width: 1090px;
}

.about__photo,
.about__content {
  width: 50%;
}

.about__photo {
  position: relative;

  img {
      position: relative;
      z-index: 2;
  }

  &:before {
      content: "";
      width: 360px;
      height: 350px;
      background-color: #EFEFEF;

      position: absolute;
      left: 1rem;
      bottom: 1rem;
      z-index: 1;
  }
}

.about__content {
  padding-left: 3.2rem;
  margin-top: 2rem;
}

.about__title {
  font-size: 2rem;
  color: #000;
  font-weight: 700;
  text-transform: uppercase;
}

.about__bigtitle {
  margin-bottom: 1.4rem;
  position: relative;
  left: -10px;

  font-size: 3.1rem;
  line-height: 1.1;
  color: fade(#000, 15%);
  font-weight: 700;
  text-transform: uppercase;
}

.about__text {
  margin-bottom: 2.8rem;

  font-size: .8rem;
  line-height: 2;
  color: #000;
  font-weight: 300;
}
.about__social-link{
  padding-left: 10px;
}
