// main: ../style.scss

.news {
  margin-top: 14rem;
  margin-bottom: 8rem;
}

.news__header {
  margin-bottom: 3.6rem;

  text-align: center;
}

.news__title {
  margin-bottom: .3rem;

  font-size: 3rem;
  color: #000;
  text-transform: uppercase;
  font-weight: 700;
}

.news__footer {
  text-align: right;
}

.news__link {
  font-size: .94rem;
  color: #000;

  &:hover {
      text-decoration: none;
  }
}

