/* comfortaa-300 - latin */
@font-face {
  font-family: 'Comfortaa';
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src:
       url('../fonts/comfortaa-v29-latin-300.woff2') format('woff2'), /* Super Modern Browsers */
       url('../fonts/comfortaa-v29-latin-300.woff') format('woff'), /* Modern Browsers */
}
